









































































































import { Component, Vue, Watch } from "vue-property-decorator";
import AppInputGroup from "./input/AppInputGroup.vue";
import AppInputPanel from "@/components/input/AppInputPanel.vue";
import AppInput from "@/components/input/AppInput.vue";
import Modal from "@/components/Modal.vue";
import {
  addSave,
  getSavesList,
  loadSave,
  removeSave
} from "@/api/SaveDatabase";
import {
    downloadAsJPG,
    downloadAsPNG, downloadAsWebM
} from "@/api/DownloadHandler";
import plot from "@/store/modules/plot";

interface File {
  text: Promise<string>;
}

@Component({
  components: {
    AppInputGroup,
    AppInputPanel,
    AppInput,
    Modal
  }
})
export default class AppExportMenu extends Vue {
  private imgType = "png";
  private videoType = "video/webm;codecs=vp9";
  private codecs = {
      'video/webm;codecs=vp9': 'webm (vp9)',
      'video/webm;codecs=vp8': 'webm (vp8)',
      'video/x-matroska;codecs=avc1': 'mkv (avc1)',
      'video/mp4; codecs=avc1': 'mp4 (avc1)',
  };

  private saveModalName = "my beautiful plot";
  private saveModalSettings = true;
  private saveModalEquations = true;

  private downloadSettings = true;
  private downloadEquations = true;

  private savesList: string[] = [];

  $refs!: {
    saveModal: any;
    loadModal: any;
  };

  private isVideoTypeSupported(mimeType:string) {
      return MediaRecorder.isTypeSupported(mimeType);
  }

  private downloadImage() {
    const plot = document.querySelector("canvas")!;
    const grid = document.querySelector("svg")!;
    if (this.imgType === "png") {
      downloadAsPNG(plot, grid);
    } else if (this.imgType === "jpg") {
      downloadAsJPG(plot, grid);
    }
    console.log("Downloading image as " + this.imgType);
  }

  private downloadVideo() {
      const plot = document.querySelector("canvas")!;
      const grid = document.querySelector("svg")!;
    downloadAsWebM(this.videoType, 5000, plot, grid);
    // downloadAsGIF();
    console.log("Downloading video as " + this.videoType);
  }
  private openSaveModal() {
    this.$refs.saveModal.open();
  }
  private closeSaveModal() {
    this.$refs.saveModal.close();
  }

  private openLoadModal() {
    this.updateSavesList();
    this.$refs.loadModal.open();
  }
  private closeLoadModal() {
    this.$refs.loadModal.close();
  }

  private downloadSave() {
    const settings = this.getSettingsAndEquations(
      this.saveModalSettings,
      this.saveModalEquations
    );
    const blob = new Blob([JSON.stringify(settings, null, 2)]);
    const url = URL.createObjectURL(blob);
    const a = document.createElement("a");
    const date = new Date();
    const dateString =
      date.getDate() +
      "-" +
      (date.getMonth() + 1) +
      "-" +
      date.getFullYear() +
      "_" +
      date.getHours() +
      "-" +
      date.getMinutes() +
      "-" +
      date.getSeconds();
    a.style.display = "none";
    a.href = url;
    a.download = "ODE-graph-" + dateString + ".json";
    a.click();
    URL.revokeObjectURL(url);
    a.remove();
  }

  private uploadSave() {
    const input = document.createElement("input");
    input.addEventListener("change", ev => {
      const files = input.files;
      if (!files || files.length !== 1) return;
      const file = files[0];
      // @ts-ignore: bug in typescript missing method
      file.text().then((file: string) => {
        const newSettings = JSON.parse(file);
        for (const [key, val] of Object.entries(newSettings)) {
          this.$store.commit("changeValue", { key, val });
        }
      });
    });
    input.type = "file";
    input.accept = ".json";
    input.click();
    input.remove();
  }

  private load(name: string) {
    loadSave(name).then(value => {
      for (const [key, val] of Object.entries(value)) {
        this.$store.commit("changeValue", { key, val });
      }
    });
    this.closeLoadModal();
  }
  private removeSave(name: string) {
    removeSave(name).then(() => {
      this.updateSavesList();
    });
  }

  private saveLocally() {
    console.log(
      this.saveModalName,
      this.saveModalSettings,
      this.saveModalEquations
    );

    const settings = this.getSettingsAndEquations(
      this.downloadSettings,
      this.downloadEquations
    );

    addSave(this.saveModalName, settings)
      .then(() => {
        console.log("success");
      })
      .catch(reason => {
        console.warn(reason);
      });
    this.closeSaveModal();
  }

  private getSettingsAndEquations(
    enableSettings: boolean,
    enableEquations: boolean
  ) {
    let settings = this.$store.state.settings;

    if (!enableSettings && !enableEquations) {
      return {};
    }

    if (!enableEquations) {
      const {
        dxString,
        dyString,
        drString,
        dtString,
        AMatrix,
        ODEType,
        ...copy
      } = settings;
      settings = copy;
    }

    if (!enableSettings) {
      const {
        dxString,
        dyString,
        drString,
        dtString,
        AMatrix,
        ODEType
      } = settings;
      settings = { dxString, dyString, drString, dtString, AMatrix, ODEType };
    }

    return settings;
  }

  private updateSavesList() {
    getSavesList().then(list => {
      console.log(list);
      this.savesList = list as string[];
    });
  }
}
